import { Router } from '@angular/router';
import { ApiCallService } from './../../api-call.service';
import { User } from './../../models/user';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {
  currentUser: User;
  constructor(private _apicallService: ApiCallService, private _router: Router) {
    this.getCurrentUserInSession();
   }

  getCurrentUserInSession() {
    this._apicallService.getCurrentUserInSession()
      .then((data) => {
        // console.log(data);
        if (data) {
          // console.log('success getting current user');
          this.currentUser = data;
          // console.log(this.currentUser);
        } else {
          // console.log('user not in session');
          this._router.navigate(['']);
        }
      })
      .catch((error) => {
        // console.log('error getting current user');
        console.log(error);
        this._router.navigate(['']);
      });
  }

  editCurrentUser() {
    // console.log('controller updating current user information');
    this._apicallService.editCurrentUser(this.currentUser)
      .then(data => {
        // console.log('then response to update current user');
        // console.log(data);
        this._router.navigate(['/home/profile/current']);
      })
      .catch(error => {
        console.log('then response to update current user');
        console.log(error);
      });
  }

  ngOnInit() {
  }

}



